html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  color: #CAC7D7;
  background: radial-gradient(#383140, #04030c);
  font-family: "Cormorant Garamond", serif;
  font-size: 16px;
  padding: 16px; }

.form-field__input {
  background: transparent;
  border: 1px solid #F4ED71;
  border-width: thin;
  color: #CAC7D7;
  font-size: 0.9rem;
  padding: 8px; }
  .form-field__input::placeholder {
    color: #CAC7D7; }

.form-field__label {
  display: inline-block;
  color: #F4ED71;
  font-size: 0.9rem;
  margin: 0 0 8px; }

.form-field__option {
  background: #383140;
  font-size: 0.9rem; }

.form-field__select {
  background: transparent;
  border: 1px solid #F4ED71;
  border-width: thin;
  color: #CAC7D7;
  font-size: 0.9rem;
  padding: 4px; }

.form-field__submit {
  background: transparent;
  border: 1px solid #F4ED71;
  border-width: thin;
  color: #CAC7D7;
  font-size: 0.9rem;
  padding: 8px;
  width: 100%; }

.form-wrapper {
  display: flex;
  justify-content: space-between; }

.header {
  display: flex; }

.header__title {
  font-size: 48px;
  letter-spacing: 1.2px;
  margin-left: 32px; }

.header__ghlogo {
  height: 120px;
  width: 120px; }

.main {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin: 50px 0;
  min-height: 100vh;
  width: 100%; }

.repository__avatar {
  display: block;
  margin: 0 auto;
  height: 180px;
  width: 180px; }

.repository__container {
  display: block;
  max-width: 900px;
  width: 80%;
  margin: 40px auto 0 auto; }

.repository__link {
  color: #CAC7D7;
  text-decoration: none; }

.repository__list {
  background: #383140;
  border-radius: 3px;
  border: 1px dotted #F4ED71;
  margin: 0;
  padding: 16px;
  list-style: none;
  color: #F4ED71;
  line-height: 1.3rem;
  font-size: 0.9rem; }

.repository__list-item {
  line-height: 1.3rem;
  word-wrap: break-word; }

.separator {
  border: none;
  display: block;
  margin: 2rem auto;
  width: 80%;
  background: rgba(138, 134, 4, 0.3);
  height: 1px;
  overflow: hidden;
  position: relative; }
  .separator::before {
    animation-duration: 2s;
    animation-timing-function: ease;
    animation-iteration-count: infinite;
    animation-name: progress;
    background: #8A8604;
    content: "";
    display: block;
    height: 1px;
    position: absolute;
    width: 80px; }

@keyframes progress {
  0% {
    transform: translateX(0px); }
  100% {
    transform: translateX(1440px); } }
